@media (min-width: 1600px) {
  *::-webkit-scrollbar {
    width: 4px;
  }
  *::-webkit-scrollbar-track {
    background: transparent;
  }
  *::-webkit-scrollbar-thumb {
    background: #5B00FB;
    border-radius: 4px;
  }
  *::-webkit-scrollbar-thumb:hover {
    background: #5B00FB;
  }
}

