html * {
  box-sizing: border-box;
}

body,
html {
  width: 100%;
  height: 100%;
}

#wrapper {
  position: relative;
  overflow: hidden;
  height: 100%;
  display: grid;
  grid-template-rows: auto minmax(0, 1fr);
}

main {
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
  z-index: 1;
}

body {
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #000000;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0 0 10px;
}

img {
  max-width: 100%;
}

p {
  margin: 0 0 10px;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

input {
  border: 0;
  outline: 0;
}

input[type="text"], input[type="password"], input[type="email"], input[type="search"] {
  -webkit-appearance: none;
}

button {
  border: 0;
  outline: 0;
  padding: 0;
  background-color: unset;
  cursor: pointer;
}

textarea {
  resize: vertical;
  -webkit-appearance: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

@media (min-width: 768px) {
  .row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
  }
}

[class^="col-"] {
  margin-bottom: 15px;
}

@media (min-width: 768px) {
  [class^="col-"] {
    margin-bottom: 30px;
    padding: 0 15px;
  }
}

@media (min-width: 768px) {
  .col-3 {
    width: 33.33%;
  }
}

@media (min-width: 1024px) {
  .col-3 {
    width: 25%;
  }
}

.col-4 {
  width: 100%;
}

@media (min-width: 768px) {
  .col-4 {
    width: 50%;
  }
}

@media (min-width: 1024px) {
  .col-4 {
    width: calc(100% / 3);
  }
}

.col-6 {
  width: 100%;
}

@media (min-width: 768px) {
  .col-6 {
    width: 50%;
  }
}

svg {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.d-flex {
  display: flex;
}
